<template><div class="content"><h1 class="post-title">使用 VuePress 搭建博客<a class="header-anchor" id="%E4%BD%BF%E7%94%A8%20VuePress%20%E6%90%AD%E5%BB%BA%E5%8D%9A%E5%AE%A2" href="#%E4%BD%BF%E7%94%A8%20VuePress%20%E6%90%AD%E5%BB%BA%E5%8D%9A%E5%AE%A2">#</a></h1><div class="metabar"><div class="metabar-item">2019/08/19</div><div class="metabar-item">路径</div><div class="metabar-item">约 9 分钟读完</div></div>
<blockquote>
<p><strong>前排提醒:</strong> 你也许需要一定的前端基础（非传统前端）才能读懂此篇文章的内容。</p>
</blockquote>
<p>我认为 Vue 的生态是有够好的了——有 Router，有 Vuex，还有更多，都是非常有用处的。其中就包含 VuePress，原本是尤雨溪先生用来提高写文档效率而写的一个小作品，现在被发展后可以用作电子书/教程编写和博客的编写。本篇文章介绍的是后者，实际上两者是相通的。</p>
<h2>流程<a class="header-anchor" id="%E6%B5%81%E7%A8%8B" href="#%E6%B5%81%E7%A8%8B">#</a></h2>
<p>VuePress 与其它静态博客软件的发布流程基本一样——写完文章后 <code>build</code> 成为静态文件。所以如果是 Hexo 或者 Hugo 用户看到这篇文章中的流程也许会感觉很熟悉，不过这里还是提到一下。</p>
<p>也正因为是静态文件，所以可以使用 GitHub 托管——当然也可以使用你自己的主机，仅需将网站根目录设置为 <code>build</code> 产生的静态文件的目录即可。这会在下文中提到。</p>
<h2>安装<a class="header-anchor" id="%E5%AE%89%E8%A3%85" href="#%E5%AE%89%E8%A3%85">#</a></h2>
<p>首先选择一个你想要用来作为博客源代码放置之处的文件夹，<strong>下文中如果没有特殊声明，所有的文件、目录均指创建在此文件夹里</strong>。</p>
<p>通过 npm 和 yarn 均可安装。本文主要使用 npm。</p>
<div class="language-sh"><pre class="language-sh"><code class="language-sh"><span class="token function">npm</span> <span class="token function">install</span> <span class="token parameter variable">-D</span> vuepress
<span class="token comment"># 或者 yarn add -D vuepress</span>
</code></pre></div>
<p>需要注意的一点是，这种方式是以本地依赖（dependencies）的形式安装 VuePress，它会在当前目录生成一个 <code>node_modules</code> 文件夹并自动安装 JavaScript、Vue 等所需要的依赖。</p>
<p>在本文后面出现的指令均无法以 <code>vuepress</code> 开头，因为 VuePress 没有被全局安装。</p>
<p>安装完成之后，就可以开始搭建了。</p>
<h2>搭建<a class="header-anchor" id="%E6%90%AD%E5%BB%BA" href="#%E6%90%AD%E5%BB%BA">#</a></h2>
<p>个人还是建议将你的博客的源代码托管到 GitHub 上，哪怕私有也好。当然，你也可以选择部署到自己的 Git 服务器上。这样做就有这些好处</p>
<ol>
<li>一个算是比较简单的双重备份吧，当你的数据消失后，可以通过 Git Log 找到；</li>
<li>一些插件需要通过 Git 的提交日志获得你提交某篇文章的具体时刻，然后才能够显示日期或者 XX ago。</li>
</ol>
<p>那么首先我们创建一个专门放置「文章」的目录（区分于「独立页面」，这样做可以避免混淆），名称可以自取，例如 <code>archives</code>、<code>articles</code>、<code>posts</code>、<code>docs</code> 等均可表达这个意思，也是传统博客程序中常用的。</p>
<div class="language-sh"><pre class="language-sh"><code class="language-sh"><span class="token function">mkdir</span> posts
<span class="token builtin class-name">cd</span> posts
</code></pre></div>
<p>然后就可以在这个文件夹里进行写作了，写作仅需在此文件夹里建立不同的 Markdown 文件，VuePress 会自动识别到你的文章。</p>
<p>VuePress 的写作使用 Markdown 文件进行，路由会自动将你的 Markdown 文件名称在实际 production 中转换成文件名 + <code>.html</code> 的形式。</p>
<p>例如 <code>docs/Hello-World.md</code> 将会被转化为 <code>docs/Hello-World.html</code>，而在根目录的 <code>Hello.md</code> 将会被转化为 <code>Hello.html</code>，这也是为什么需要专门创建一个文件夹用来放置文章的原因，根目录里只需要放置独立页面即可，这样从路由层面看来会更加友好。</p>
<p>到目前为止，你的博客目录应该为这个样子</p>
<div class="language-plaintext"><pre class="language-plaintext"><code class="language-plaintext">.
├─package-lock.json
├─package.json
├─node_modules
└─posts
</code></pre></div>
<h2>写作与发布<a class="header-anchor" id="%E5%86%99%E4%BD%9C%E4%B8%8E%E5%8F%91%E5%B8%83" href="#%E5%86%99%E4%BD%9C%E4%B8%8E%E5%8F%91%E5%B8%83">#</a></h2>
<p>如果你的环境已经搭建完毕，那么你可以选择在纯 Markdown 下进行写作，然后直接 <code>push</code> 到你的仓库中去。</p>
<p>而如果你想要实时预览自己的文章在博客上的样子，或者调试一些应用层面的东西，你就需要启动开发服务器。</p>
<p>与 Vue 的开发服务器相同，VuePress 的服务器也是支持热重载的。</p>
<div class="language-sh"><pre class="language-sh"><code class="language-sh"><span class="token comment"># 如果之前 npm install 为全局安装</span>
<span class="token comment"># npm install -D -g vuepress</span>
vuepress dev <span class="token builtin class-name">.</span>
<span class="token comment"># 如果之前 npm install 是以本地依赖安装</span>
<span class="token comment"># npm install -D vuepress</span>
npx vuepress dev <span class="token builtin class-name">.</span>
</code></pre></div>
<p>这样，在默认情况下会在 <a href="https://localhost:8080" class="external-link" target="_blank" rel="noopener noreferrer">https://localhost:8080<span class="external-link-icon mdi mdi-launch"></span></a> 开放你的开发服务器，访问即可看到你的网站。</p>
<p>最后，写完了就可以进行构建了——将你的网站样式和内容编译成静态网页。</p>
<div class="language-sh"><pre class="language-sh"><code class="language-sh">vuepress build <span class="token builtin class-name">.</span>
<span class="token comment"># or</span>
npx vuepress build <span class="token builtin class-name">.</span>
</code></pre></div>
<p>默认情况下这个静态网页会被保存到 <code>.vuepress/dist</code> 内，在后文会介绍修改方法。</p>
<h2>配置、主题、插件<a class="header-anchor" id="%E9%85%8D%E7%BD%AE%E3%80%81%E4%B8%BB%E9%A2%98%E3%80%81%E6%8F%92%E4%BB%B6" href="#%E9%85%8D%E7%BD%AE%E3%80%81%E4%B8%BB%E9%A2%98%E3%80%81%E6%8F%92%E4%BB%B6">#</a></h2>
<p>VuePress 之所以适合开发者，是因为它可以通过配置、主题和插件三方面高程度个性化——当然，是在你会 Vue 而且会看 VuePress 的 API 的前提下。</p>
<p>VuePress 的配置文件存储在 <code>.vuepress</code> 里，一般情况下是没有这个文件夹的，我们可以自己创建。</p>
<p>进入后，创建 <code>config.js</code>，这就是 VuePress 的配置文件。</p>
<p><code>config.js</code> 在写入一个语句之后即可当作一般的 JSON 文件来使用——它比 JSON 要宽松得多。</p>
<div class="language-js"><pre class="language-js"><code class="language-js">module<span class="token punctuation">.</span>export <span class="token operator">=</span> <span class="token punctuation">{</span>
    <span class="token literal-property property">title</span><span class="token operator">:</span> <span class="token string">"Subilan's Blog"</span><span class="token punctuation">,</span>
    <span class="token comment">// ...</span>
<span class="token punctuation">}</span>
</code></pre></div>
<p>具体配置项目可以参考 VuePress 官方文档的<a href="https://vuepress.vuejs.org/zh/guide/basic-config.html#%E9%85%8D%E7%BD%AE%E6%96%87%E4%BB%B6" class="external-link" target="_blank" rel="noopener noreferrer">配置<span class="external-link-icon mdi mdi-launch"></span></a></p>
<p>要切换主题，首先使用 npm 安装你想要的主题，比如</p>
<div class="language-sh"><pre class="language-sh"><code class="language-sh"><span class="token function">npm</span> <span class="token function">install</span> vuepress-theme-example --save-dev
<span class="token comment"># yarn add vuepress-theme-example --dev</span>
</code></pre></div>
<p>然后在 <code>config.js</code> 里添加一行</p>
<div class="language-js"><pre class="language-js"><code class="language-js">module<span class="token punctuation">.</span>export <span class="token operator">=</span> <span class="token punctuation">{</span>
    <span class="token comment">// ...</span>
    <span class="token literal-property property">theme</span><span class="token operator">:</span> <span class="token string">"example"</span><span class="token punctuation">,</span>
<span class="token punctuation">}</span>
</code></pre></div>
<p>即可。</p>
<p>在这里需要注意的一点是，VuePress 的所有主题、Plugin，根据官方的建议，均是以 <code>vuepress-theme-</code> 和 <code>vuepress-plugin-</code> 开头。也正因此，安装以他们开头的主题或插件，在填写的时候则可以省略 <code>vuepress-theme-</code> 这样的开头。当然，这只是一般情况，具体请以你所看到的主题或插件为准。</p>
<p>关于 VuePress 原版主题的相关配置可以看官方文档的<a href="https://vuepress.vuejs.org/zh/default-theme-config/" class="external-link" target="_blank" rel="noopener noreferrer">默认主题<span class="external-link-icon mdi mdi-launch"></span></a>。</p>
<h2>参考<a class="header-anchor" id="%E5%8F%82%E8%80%83" href="#%E5%8F%82%E8%80%83">#</a></h2>
<p>这是阅读完文章后，我个人推荐去参考的一些文档，以及一些可能需要的插件。</p>
