<li><a href="https://vuepress.vuejs.org/zh/config/#%E5%9F%BA%E6%9C%AC%E9%85%8D%E7%BD%AE" class="external-link" target="_blank" rel="noopener noreferrer">config.js 里完整的配置项有多少、哪些，有何作用？<span class="external-link-icon mdi mdi-launch"></span></a></li>
<li><a href="https://www.google.com/search?q=vuepress-theme+site%3Agithub.com" class="external-link" target="_blank" rel="noopener noreferrer">Google 搜索 VuePress 的主题<span class="external-link-icon mdi mdi-launch"></span></a></li>
<li><a href="https://github.com/meteorlxy/awesome-vuepress" class="external-link" target="_blank" rel="noopener noreferrer">meteorlxy/awesome-vuepress<span class="external-link-icon mdi mdi-launch"></span></a> - 丰富的 VuePress 周边生态推荐</li>
